* {
  box-sizing: border-box;
  padding: 2;
  margin: 2;
}

#bg {
  display: flex;
  background-color: goldenrod;
  background-image: url(./components/assets/portfolioBG.png);
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;

  padding: 10vh;
  padding-bottom: 25vh;
  
}

img#bg {
  width: 100%;
}

.nav-tabs
{
  display: flex;
  justify-content: center;
  padding: 5px;
  background: repeating-linear-gradient( 45deg, black, white );
  height: 100px;
  margin-top: 0px;
  margin-bottom: 0px;
  width: 100%;
}





img#myPic  {
  display: inline-block;
  max-width: 500px;
  max-height: 460px;
  box-shadow: 20px 10px 10px black;
}

#aboutMe {
  display: flex;
  margin-left: 150px;
  border: 5px;
  padding: 5px;
  border-style: solid;
  border-color: black;
  border-radius: 20px;
  background-color: lightgray;
  font-size: 20px;
  box-shadow: 20px 10px 10px black;
  width: 60%;
  height: fit-content;
}

#myName {
  display: flex;
  justify-content: left;
}

h1 {
  align-content: flex-start;
  font-size: 50px;
  text-shadow: 0 0 3px goldenrod;
  padding: 10px;
  margin-top: 0;


}


h3 {
  margin-left: 25px;
  font-size: 36px;
  margin-bottom: 0;
  text-shadow: 0 0 3px goldenrod;
}

li {
  margin: 0 20px;
  margin-top: 20px;
  color: white;
}

li a {
  color: black;
  text-decoration: none;
  font-size: 36px;
  font-style: bold;
  text-shadow: 0 0 3px goldenrod;
}

li::marker{
  font-size: 0;
}

a.nav-link.active {
  color: goldenrod;
  text-shadow: 0 0 3px black;
}
/* li a:visited {
  color: purple;
  
} */

li a:hover {
  color: goldenrod;
  
}

img {
  object-fit: contain;
  border: 5px;
  border-style: solid;
  border-color: black;
  border-radius: 20px;
  box-shadow: 10px;
}



.project {
  flex: 1 0 50%;
  flex-direction: row;
  overflow-wrap:inherit;
  align-items: center;
  text-align: center;
  margin-left: 10%;


  
}

#project {
  width: 600px;
  height: 300px;
  border-width: 0px;
  border-radius: 1000px;
  box-shadow: 20px 10px 10px black;
 

}

a img:hover {
  transform: scale(1.5);
}

#gitHub{
  position: relative;
  left: -80px;
  top: -100px;
  max-width: 100px;
  max-height: 100px;
  border: 0px;
}

.footer {
  position: fixed;
  bottom: 0;
  left: 0;
  display: flex;
  justify-content: flex-end;
  padding: 5px;
  background: repeating-linear-gradient( 45deg, white, black );
  height: 100px;
  margin-top: 0px;
  margin-bottom: 0px;
  width: 100%
}

#footer li::marker{
  font-size: 0
}

#githubFooter, #linkedInFooter, #soFooter{
  height: 50px;
  width: 50px;
  border-width: 3px;
  border-color: white;
  background-color: goldenrod;
}

#form {
  display: flex;
  flex-direction: column;
  justify-self: flex-end;
  align-items: center;
  border: 5px;
  border-style: solid;
  border-color: black;
  padding: 20px;
  padding-Top: 0px;
  background-color: lightgray;
  border-radius: 20px;
  box-shadow: 20px 10px 10px black;
  margin-left: 35vh;
  margin-top: 5vh

}

#resume {
  display: flex;
  flex-direction: column;
  /* border-style: solid;
  border-color: black;
  border-radius: 20px;
  background-color: lightgray; */
  padding: 15px

}

#skills li {
  font-size: 20px;
  color: black;
}

#skills li::marker{
  font-size: 20px;
}


#resumeDl {
  font-size: 20px;
}
